.calc_radio {
  margin: 7px 0;

  input[type="radio"] {
    display: none;

    &:checked {

      + label {

        &::before {
          content: "";
          width: 8px;
          height: 3px;
          border-left: 2px solid $primary;
          border-bottom: 2px solid $primary;
          position: absolute;
          left: 3px;
          top: 7px;
          transform: rotate(-45deg);
          z-index: 1;
        }
      }
    }
  }

  label {
  font-size: px-to-rem(17px);
  line-height: px-to-rem(24px);
  position: relative;
  padding-left: 25px;
  cursor: pointer;
  display: inline;

    &::after {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      border: 1px solid $primary;
      border-radius: 5px;
      box-sizing: border-box;
      background-color: transparent;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }

}

.calc_group {
  padding: 0;
  border: none;
  margin: 20px 0;
}

.calc_title {
  font-size: px-to-rem(14px);
  line-height: px-to-rem(16px);
  font-weight: 700;
  margin-bottom: 10px;
}

.calc_range {
  display: none;

  label {
    @include visually-hidden;
  }
}

.calc_square {
  background-color: transparent;
  display: block;
  width: 100%;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 5px;
  text-align: center;
  font-size: px-to-rem(14px);
  line-height: px-to-rem(16px);
  color: $white;
  padding: 12px 0;
}

.calc_btn {
  @include btn {

  }
}

.calc_result {
  text-align: center;
  font-size: px-to-rem(17px);
  line-height: px-to-rem(24px);
  margin: 20px 0;
}

@media (min-width: 480px) {
  .calc_wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  .calc_title, .calc_input {
    flex-basis: 100%;
    width: 100%;
  }

  .calc_radio {
    margin-right: 24px;

    &:last-child {
      margin-right: 0;
    }
  }

  .calc_btn {
    flex-basis: 164px;
    width: 164px;
    margin-right: 25px;
  }

  .calc_result {
    text-align: left;
    font-size: px-to-rem(14px);
    line-height: px-to-rem(16px);
  }

}

@media (min-width: 1024px) {
  .calculator_title {
    text-align: left;
    padding-left: 465px;
  }

  .calculator_img {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(calc(-50% + 25px));    
  }

  .calculator_form {
    padding-left: 215px;
    padding-top: 5px;
    padding-bottom: 25px;
    margin-left: 250px;
  }

  .calc_input {
    display: none;
  }

  .calc_range {
    display: flex;
    align-items: center;
    flex-basis: 100%;
    margin: 7px 0;

    output {
      background-color: transparent;
      display: block;
      width: 67px;
      border: 1px solid rgba(255, 255, 255, 0.2);
      border-radius: 5px;
      text-align: center;
      font-size: px-to-rem(14px);
      line-height: px-to-rem(16px);
      color: $white;
      padding: 3px 0;
      margin-right: 24px;
    }
  }

  input[type="range"] {
    width: 280px;
    flex-basis: 280px;
    margin: 10px 0;
    appearance: none;

    &::-webkit-slider-runnable-track {
      background-color: $white;
      width: 100%;
      height: 4px;
      cursor: pointer;
      border-radius: 10px;
    }

    &::-moz-range-track {
      background-color: $white;
      width: 100%;
      height: 4px;
      cursor: pointer;
      border-radius: 10px;
    }

    &::-ms-track {
      background-color: $white;
      width: 100%;
      height: 4px;
      cursor: pointer;
      border-radius: 10px;
    }

    &::-webkit-slider-thumb {
      width: 10px;
      height: 10px;
      background-color: $primary;
      border-radius: 10px;
      cursor: pointer;
      appearance: none;
      margin-top: -3px;
    }

    &::-moz-range-thumb {
      width: 10px;
      height: 10px;
      background-color: $primary;
      border-radius: 10px;
      cursor: pointer;
      appearance: none;
      margin-top: -3px;
    }

    &::-ms-thumb {
      width: 10px;
      height: 10px;
      background-color: $primary;
      border-radius: 10px;
      cursor: pointer;
      appearance: none;
      margin-top: -3px;
    }

  }

}