.calculator {
  background-color: #181818;
  color: $white;
}

.calculator_wrap {
  padding-top: 100px;
  padding-bottom: 10px;
  position: relative;
}

.calculator_title {
  font-size: px-to-rem(32px);
  line-height: px-to-rem(37px);
  color: $primary;
  text-align: center;
  margin: 0 0 20px 0;
  font-weight: 700;
}

.calculator_img {
  display: block;
  max-width: 100%;
  margin: 24px auto 30px;
  height: auto;
}


@media (min-width: 1024px) {
  .calc {
    background-color: $black;
    border-radius: 5px;
  }
}