.advanages {
  list-style: none;
}

.advanages_item {
  padding: 24px;
  box-sizing: border-box;
  background: rgba(0, 0, 0, .3);
  border-radius: 5px;
  text-align: center;
  margin-bottom: 24px;

  &:last-child {
    margin-bottom: 0;
  }
}

.advanages_text {
  font-size: px-to-rem(17px);
  line-height: px-to-rem(24px);
  margin: 10px 0 2px;
}

@media (min-width: 1024px) {
  .advanages {
    display: flex;
    justify-content: center;
    max-width: 744px;
    margin: 0 auto;
    z-index: 1;

    &::after {
      position: absolute;
      content: "";
      background:rgba(0, 0, 0, .3);
      height: 145px;
      width: 88%;
      left: 0;
      z-index: -1;
      border-radius:0 72px 72px 0;
    }
  }

  .advanages_item {
    background:none;
    border-radius: 0;
    flex-basis: 25%;
    margin-bottom: 0;
    padding-left: 0;
    padding-right: 0;
  }

  .advanages_text {
    font-size: px-to-rem(16px);
    line-height: pz-to-rem(24px);
  }
}