.contacts {
  background: url("../img/map.jpg") 65% 150px $black no-repeat;
  background-size: cover;
  position: relative;
  z-index: 1;
  
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 100%), rgba(24, 24, 24, 0.8);
      z-index: -1;
    
  }
}

.contacts_wrap {
  padding-top: 130px;
  padding-bottom: 100%;
}

.contacts_title {
  font-size: px-to-rem(32px);
  line-height: px-to-rem(37px);
  text-align: center;
  color: $primary;
  margin: 0 0 20px 0;
  font-weight: 700;
}

.contacts_info {
  background-color: $white;
  border-radius: 5px;
  box-sizing: border-box;
  padding: 24px;
}

.contacts_phone, 
.contacts_mail,
.contacts_address {

  margin: 0 0 20px 0;

  span {
    display: block;
    font-size: px-to-rem(14px);
    line-height: px-to-rem(16px);
    font-weight: 700;
    padding-left: 30px;
    background: url(../img/tel.svg) left center no-repeat;
  }

  a {
    text-decoration: none;
    color: $black;
    font-size: px-to-rem(17px);
    line-height: px-to-rem(24px);
    display: inline-block;
    margin: 0 0 0 30px;
    transition-duration: 500ms;
    transition-property: color;

    &:hover {
      color: $primary;
    }
  }

}

.contacts_mail {
  span {
    background-image: url("../img/mail.svg");
  }
}

.contacts_guest {
  font-weight: 700;
}

.contacts_address {
  span {
    background-image: url("../img/map.svg");
    line-height: px-to-rem(24px);

    &:last-child {
      background: none;
      font-size: px-to-rem(17px);
      line-height: px-to-rem(24px);
      font-weight: 400;
      margin-top: 10px;
    }
  }
}

.contacts_shema {
  font-size: px-to-rem(17px);
  line-height: px-to-rem(24px);
  margin-bottom: 0;
  margin-left: 30px;

  span {
    display: block;
  }

  a {
    text-decoration: none;
    color: $primary;
  }
}

@media (min-width: 768px) {

  .contacts {
    background-position: 75% 150px;
  }
}

@media (min-width: 1024px) {
  
  .contacts {
    background-position: center top;
  }

  .contacts_info {
    max-width: 358px;
    margin-top: 30px;
  }

  .contacts_wrap {
    padding-bottom: 10px;
  }

  .contacts_title {
    max-width: 455px;
    text-align: left;
    margin: 20px 0;
  }

}